
import XHeader from 'vux/src/components/x-header/index.vue'

export default{
components: {
XHeader
},
data(){
return {
RECORD_ID:'',
objData:{},
resultArray:['否','是','倾向是'],
scoreList: [
{info: '平和质',score: 0},
{info: '气虚质',score: 0},
{info: '阳虚质',score: 0},
{info: '痰湿质',score: 0},
{info: '湿热质',score: 0},
{info: '血瘀质',score: 0},
{info: '气郁质',score: 0},
{info: '特禀质',score: 0},
{info: '阳虚质',score: 0},
],
resultList: [
{info: '平和质',result: '否'},
{info: '气虚质',result: '否'},
{info: '阳虚质',result: '否'},
{info: '痰湿质',result: '否'}
]
}
},
mounted(){
var routerParams = this.$route.params;
this.RECORD_ID = routerParams.RECORD_ID;
this.initInfo();
},
methods :{
initInfo(){

this.$ajax.post('',{
'MSH.1':'lnrzyjkgl',
'MSH.2':'bsbg',
'GRDAID':this.$root.loginInfo.grdaid,
'CHZBM':this.$root.loginInfo.CHZBM,
'RECORD_ID':this.RECORD_ID,
'token':this.$root.loginInfo.token,
}).then(data => {
this.$vux.loading.hide()
if(typeof data !='undefined'){
this.objData = data;
this.scoreList[0].score = data.DFPHZ;
this.scoreList[1].score = data.DFQXZ;
this.scoreList[2].score = data.DFYAXZ;
this.scoreList[3].score = data.DFTSZ;
this.scoreList[4].score = data.DFRSZ;
this.scoreList[5].score = data.DFXYZ;
this.scoreList[6].score = data.DFQYZ;
this.scoreList[7].score = data.DFTBZ;
this.scoreList[8].score = data.DFYAXZ;

this.resultList[0].result = this.resultArray[data.SFPHZ];
this.resultList[1].result = this.resultArray[data.SFQXZ];
this.resultList[2].result = this.resultArray[data.SFYAXZ];
this.resultList[3].result = this.resultArray[data.SFTSZ];
}
}).catch(error => {
console.log(error);
});
}

},
}



